.MainSidebar {
  width: 320px;
  min-height: var(--min-height);
  .main-sidebar-statics {
    display: inline-flex;
    margin: 16px 0;
    padding: 0 10px;
    div {
      font-size: 15px;
      border-radius: 8px;
      -webkit-border-radius: 8px;
      -moz-border-radius: 8px;
      -ms-border-radius: 8px;
      -o-border-radius: 8px;
      display: flex;
      align-items: center;
      color: var(--white);
      margin: 0 4px;
      padding:8px 6px;
      img {
        margin-right: 6px;
      }
      svg {
        vertical-align: middle;
        margin: 0 10px;
      }
      span{
        font-family: var(--body-font);
      }
    }
    div.open-avalnche {
      background-color: var(--bg2);
    }
    div.view-balance {
      border: 1px solid var(--primary-color);
    }
  }
  form {
    padding: 0 4%;
    margin-top: 20px;
    h1 {
      font-size: 18px;
      font-family: var(--body-font-secondary);
      font-weight: var(--font-medium);
      img {
        margin-right: 8px;
      }
    }
    input {
      width: 100%;
      padding: 12px 18px;
      font-size: 15px;
      margin: 8px 0;
    }

    input.input-read-only {
      color: var(--white);
      font-family: var(--body-font);
    }
    input.input-write-only {
      border-radius: 8px;
      -webkit-border-radius: 8px;
      -moz-border-radius: 8px;
      -ms-border-radius: 8px;
      -o-border-radius: 8px;
      color: var(--text-color);
      background-color: var(--bg2);
      font-family: var(--font-family-secondary);
    }
    .form-control-button {
      margin-top: 24px;
      text-align: center;

      button {
        font-family: var(--font-family-secondary);
        padding: 8px 12px;
        border-radius: 8px;
        -webkit-border-radius: 8px;
        -moz-border-radius: 8px;
        -ms-border-radius: 8px;
        -o-border-radius: 8px;
        margin: 0 8px;
        display: inline-flex;

        img {
          margin-right: 10px;
        }
        span {
          color: var(--white);
          font-size: 14px;
        }
      }
      button.btnCustomlink {
        background-color: var(--primary-color);
      }
      button.btnCancel {
        border: 1px solid rgba(255, 255, 255, 0.3);
      }
    }
  }
}
