 // banner section
 .section-banner {
    margin: 0.75em 0;
    .banner-container {
      width: 100%;

      .banner {
        position: relative;
        display: flex;
        justify-content: space-between;
        padding: 10px 2%;
        background-color: var(--banner-color);
        border-radius: 10px;
        -webkit-border-radius: 10px;
        -moz-border-radius: 10px;
        -ms-border-radius: 10px;
        -o-border-radius: 10px;
        span.banner-close-icon {
          width: 30px;
          height: 30px;
          position: absolute;
          right: -5px;
          top: -10px;
          font-size: 20px;
          color: #000;
          background-color: #fff;
          border-radius: 50%;
          -webkit-border-radius: 50%;
          -moz-border-radius: 50%;
          -ms-border-radius: 50%;
          -o-border-radius: 50%;
          text-align: center;
          cursor: pointer;
        }
        p {
          color: #2b2557;
          margin-top: 0.5em;
        }
        button {
          background-color: #fff;
          padding: 8px 16px;
          font-size: 12px;
          color: #2b2557;
          font-family: var(--body-font-secondary);
          font-style: normal;
          font-weight: var(--font-semi-bold);
          letter-spacing: 0.35px;
          border-radius: 6px;
          -webkit-border-radius: 6px;
          -moz-border-radius: 6px;
          -ms-border-radius: 6px;
          -o-border-radius: 6px;
          margin-top: 1.25em;
        }
      }
    }
  }