.header-container {
 width: 100%;
 position: fixed;
 top: 0;
 left: 0;
 right:0;
 z-index: var(--z-index-high);
 .header {
   background-color: var(--primary-color);
   padding:4px auto;
   height: var(--header-height);
   text-align: center;

    h2{
      font-size: 14px;
      color: #fff;
      line-height: var(--header-height);
    }
  }
}