body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --header-height: 2.8rem;
  --min-height: 100vh;
  --width: 100%;

  /*======Colors======*/
  --white: #fff;
  --body-color: #e5e5e5;
  --primary-color: #3772ff;
  --app-color: #000;
  --min-height: 100vh;
  --text-color: #fff;
  --banner-color: #abe8ff;
  --text-color: #808191;
  --bg1: #191b20;
  --bg2: #242731;
  --bg3: #353945;

  /*======FontsandTypography======*/
  --body-font: "Poppins", sans-serif;
  --body-font-secondary: "Inter", sans-serif;

  /*0.5rem=8px,1rem=16px,1.5rem=24px*/

  /*======FontWeight======*/

  --font-medium: 500;
  --font-semi-bold: 600;

  /*======Zindex======*/ /*======Fontsizeforlargetdevices======*/
  --z-index-high: 99;
}

/*====== Base ====== */

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: scroll;
}

body {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  background-color: var(--body-color);
  color: var(--text-color);
  width: var(--width);
  min-height: var(--min-height);
}

h1,
h2,
h3,
h4 {
  color: var(--white);
  font-weight: var(--font-medium);
}

li {
  list-style-type: none;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}

button,
input {
  background: transparent;
  border: none;
  outline: none;
}
button {
  cursor: pointer;
}

/*====== Reusable Css Classes ====== */

.grid {
  display: grid;
  gap: 1.5rem;
}

.flex {
  display: flex;
}

section.section-divider {
  margin: 1em 0;
}
