nav {
  color: var(--text-color);

  .nav-title {
    padding-left: 3%;
    margin:14px 0 12px 0;

    h1 {
      font-size: 18px;
      img {
        margin-right: 14px;
      }
    }
  }
  ul.nav-tabs {
    margin: 0 2%;
    li {
      display: flex;
      align-items: center;
      padding: 8px 0;
      transition: padding-left 0.3s ease;
      -webkit-transition: padding-left 0.3s ease;
      -moz-transition: padding-left 0.3s ease;
      -ms-transition: padding-left 0.3s ease;
      -o-transition: padding-left 0.3s ease;
      color: rgba(255, 255, 255, 0.5);
      cursor: pointer;

      img {
        padding: 0 1em 0px 8px;
      }
      h3 {
        font-size: 14px;
        font-weight: 500;
        color: var(--text-color);
        font-family: var( --body-font-secondary);
      }
    }
    li:hover,
    li.active {
      background-color: #353945;
      border-radius: 8px;
      -webkit-border-radius: 8px;
      -moz-border-radius: 8px;
      -ms-border-radius: 8px;
      -o-border-radius: 8px;
      padding-left: 16px;
      color: var(--white);
    }
    li:hover > h3,
    li.active > h3 {
      color: var(--white);
    }

  }
}
