.sidebar-container {
  flex: 1;
  border-right: 1px solid rgba(255, 255, 255, 0.4);
  position:fixed;
  width:270px;
 
  .sidebar {
    height: calc(100vh - var(--header-height));
    width: 90%;
    margin:0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding:10px 0;
  }
}
