.sidebar-bottom-container {
  margin-top: 2em;
  .sidebar-bottom {
    display: flex;
    justify-content: space-around;

    button {
      display: block;
      padding: 10px;
      width: 110px;
      height: 35px;
      cursor: pointer;
      border-radius: 8px;
      -webkit-border-radius: 8px;
      -moz-border-radius: 8px;
      -ms-border-radius: 8px;
      -o-border-radius: 8px;

      span {
        font-size: 14px;
        font-weight: 600;
      }
    }

    button.btnFirst {
      background-color: var(--bg3);
      color: var(--text-color);
      display: flex;
      align-items: center;
      justify-content: space-around;

      span {
        color: var(--white);
      }
    }
    button.btnLast {
      background-color: var(--banner-color);
      span {
        color: var(--primary-color);
      }
    }
  }
}
