.main-top-section {
  width: 100%;
  padding: 0px 1% 10px 1%;
  margin-top: 1.5em;
  .section-left {
    float: left;
    .section-title {
      padding: 0 0 8px 0;
      border-bottom: 2px solid #3772ff;

      h3 {
        font-weight: 18px;
        font-family: var(--body-font-secondary);
        font-weight: var(--font-semi-bold);
        letter-spacing: 0.25px;
      }
    }
  }
  .section-right {
    float: right;
    div {
      display: flex;
    }
    button {
      display: inline-block;
      color: #fff;
      margin: 0 10px;
    }
    button.btnWallet {
      display: flex;
      align-items: center;
      img {
        margin-right: 12px;
      }
    }
    button.btnTire {
      background-color: var(--banner-color);
      color: #3772ff;
      border-radius: 3px;
      -webkit-border-radius: 3px;
      -moz-border-radius: 3px;
      -ms-border-radius: 3px;
      -o-border-radius: 3px;
      padding: 4px 8px;
      text-align: center;
      width: 60px;
      height: 25px;
    }
  }
}
