.card {
    background-color: var(--bg1);
    opacity: 0.8;
    border-radius: 16px;
    padding: 18px 4%;
    img {
      margin-right: 10px;
    }
    h1 {
      font-size: 20px;
      color: var(--white);
      margin-bottom: 16px;
      display:flex;
      align-items: center;
    }
    h3 {
      font-size: 16px;
      color: var(--text-color);
      font-family: var(--body-font-secondary);
      font-weight: var(--font-medium);
      letter-spacing: 0.32px;
      margin-bottom: 12px;
    }
    form {
      background-color: var(--bg2);
      padding: 10px 12px;
      display: flex;
      align-items: center;

      input {
        font-family: var(--body-font-secondary);
        font-weight: var(--font-medium);
        letter-spacing: 0.5px;
        width: 100%;
        font-size: 14px;
        color: var(--white);
      }
      button {
        padding: 5px 10px;
        img {
          margin-left: 16px;
        }
      }
    }
  }